import {searchCustomSkill, searchSkill, searchTitle} from "@/utils/SourceDataUtil";
import store from "@/store";
import {skillType} from "@/utils/TypeValueConverter";

export class Role {
    constructor(role) {
        this.base = {
            name: '',
            id: 0,
            round: 4,
            ke: 1,
            classType: '',
        }
        this.config = {
            title: null,
            talent: null,
            effect: null,
            triggerSkill: null,
            twoHands: false,
            harmony: false,
        }
        this.options = {
            titleCustom: false,
            talentCustom: false,
            effectCustom: false,
            triggerCustom: false,
            titleScoreOnly: false,
            titleDescShow: true,
            talentKeOnly: false,
            talentDescShow: true,
            effectKeOnly: false,
            effectDescShow: true,
            triggerKeOnly: false,
            triggerDescShow: true,
        }
        if (role) {
            Object.assign(this.base, role.base)
            Object.assign(this.config, role.config)
            Object.assign(this.options, role.options)
        }
    }
    getTitleGrade() {
        if (!this.config.title) {
            return 0
        }
        const id = this.config.title.split(':')[1]
        return searchTitle(id)?.grade || 0
    }
    getTitleDesc() {
        if (!this.config.title) {
            return ''
        }
        const id = this.config.title.split(':')[1]
        return searchTitle(id)?.desc || ''
    }
    getTitle() {
        const nil = {
            name: '',
            desc: '',
            grade: 0,
        }
        if (!this.config.title) {
            return nil
        }
        const id = this.config.title.split(':')[1]
        const title = searchTitle(id)
        if (!title) {
            return nil
        }
        return {
            name: title.name,
            desc: title.desc,
            grade: title.grade,
        }
    }
    getSkill(key) {
        const nil = {
            id: -1,
            name: '',
            desc: '',
            grade: 0,
        }
        if (!key) {
            return nil
        }
        const [type, id] = key.split(':')
        if (type == 'std') {
            const skill = searchSkill(id)
            if (!skill) {
                return nil
            }
            return {
                id: skill.id,
                name: skill.name,
                desc: skill.type == 7 ? skill['text_equip'] : skill['text_use'],
                grade: Number(skill.grade),
            }
        } else if (type == 'custom') {
            const customSkill = searchCustomSkill(id)
            if (!customSkill) {
                return nil
            }
            const grade = customSkill.skill.calcGrade()
            return {
                id: customSkill.id,
                name: customSkill.name,
                desc: grade.desc,
                grade: grade.totalGrade,
            }
        }
    }
}

export function getTitleList() {
    return store.state.TitleConfigSource
        .map((item) => {
            return {
                'text': `[${item.skillId}]${item.name}`,
                'value': `std:${item.skillId}`,
                'grade': item.grade,
            }
        })
}

export function getTalentList(custom) {
    if (custom) {
        return store.state['customSkill'].customSKills
            .filter((item) => item.skill.base.type === '')
            .map((item) => {
                return {
                    'text': `${item.name}`,
                    'value': `custom:${item.name}`,
                    'ke': true,
                }
            })
    }
    return Object.values(store.state.SkillSource)
        .filter((item) => item.type === 7)
        .map((item) => {
            return {
                'text': `[${item.id}]${item.name}`,
                'value': `std:${item.id}`,
                'ke': item.ke == '1',
                'grade': item.grade,
            }
        })
}

export function getEffectList(custom) {
    if (custom) {
        return store.state['customSkill'].customSKills
            .filter((item) => item.skill.base.type === '')
            .map((item) => {
                return {
                    'text': `${item.name}`,
                    'value': `custom:${item.name}`,
                    'ke': true,
                }
            })
    }
    return Object.values(store.state.SkillSource)
        .filter((item) => item.type === 8)
        .map((item) => {
            return {
                'text': `[${item.id}]${item.name}`,
                'value': `std:${item.id}`,
                'ke': item.ke == '1',
                'grade': item.grade,
            }
        })
}

export function getTriggerSkillList(type) {
    return Object.values(store.state.SkillSource)
        .filter((item) => type ? skillType(item.type) == type : item.type >= 0 && item.type <= 4)
        .filter((item) => item['trigger_effect'])
        .map((item) => {
            return {
                'text': `[${item.id}]${item.name}`,
                'value': `std:${item.id}`,
                'ke': item.ke == '1',
                'trigger_effect': item['trigger_effect'],
            }
        })
}